<template>
    <div class="echarts">
        <h3>echarts</h3>
        <div ref="main" style="height:300px" @click="show"></div>
    </div>
</template>
<script>
import echarts from 'echarts';

export default {
    methods:{
        show(){
            console.log('vue里面的 div事件')
        },
        show2(params){
            console.log('maychart事件',params.name)
        }
    },
    mounted(){
         // 基于准备好的dom，初始化echarts实例
        //var myChart = echarts.init(document.getElementById('main'));
        console.log(this.$refs)
        var myChart = echarts.init(this.$refs.main);
        myChart.showLoading()

        axios({
            url:'/data/echarts.json'
        }).then(res=>{
            console.log(res.data);
            myChart.hideLoading()
            myChart.setOption(res.data);
        })

      
     }
}
</script>

