<template>
  <div class="column">
    <!-- <List :list="column" dataName="column"></List> -->
    <ul>
      <li v-for="item in $store.state.column" :key="item._id">
          <router-link href="javascript:;" :to="'/detail/'+item._id">
            <h2>{{item._id}}.{{item.title}}</h2>
            <p>{{item.des}}</p>
          </router-link>
          <img src="../assets/img/xing.png" alt="" @click="ADD_ITEM(item)">
      </li>
    </ul>
  </div>
</template>
<script>
import List from '../components/List'
import {mapActions,mapGetters} from 'vuex'
export default {
  components:{List},
  methods:mapActions([
    'ADD_ITEM'
  ]),
  mounted(){
    this.$store.dispatch("UPDATE_COLUMN");
  },
  computed:mapGetters(['column']),
}
</script>
<style scoped>
.column{
  padding-top:.7rem;
}
</style>

