import fillzero from './fillzero'

// export default (time)=>{
//     return '时间123'
// }

export default (time)=>{
    var dataTime="";
    var d=new Date();
    d.setTime(time);
    var y=d.getFullYear();
    var m=fillzero(d.getMonth()+1);
    var day=fillzero(d.getDate());
    var hour=fillzero(d.getHours());
    var min=fillzero(d.getMinutes());
    var sec=fillzero(d.getSeconds());
    dataTime=y+"年"+m+"月"+day+"日"+hour+"时"+min+"分"+sec+"秒";

    return dataTime
}